import React from 'react';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import Slider from '@mui/material/Slider';
import Box from "@mui/material/Box";


const SoundRange = (classes) => {
    const first = localStorage.getItem('barRange') === null ? 0 : Number(localStorage.getItem('barRange').split(",")[0]);
    const last = localStorage.getItem('barRange') === null ? 50 : Number(localStorage.getItem('barRange').split(",")[1]);

    const [value, setValue] = React.useState([first, last]);

    const handleChange = (event, newValue) => {
        setValue(newValue);
        if (localStorage.getItem('barRange') != value) {
            localStorage.setItem('barRange', value);
        }
    };

    const marks = [
        {
            value: 1,
            label: '1',
        },
        {
            value: 100,
            label: '100',
        },
    ];
    if (localStorage.getItem("micOrSound") !== "toneGenerator" || localStorage.getItem("pinkNoise") === "true") {
        return (
            <FormControl className={"all-form-inputs " + classes.formControl}>
                <Box
                    component="form"
                    sx={{
                        '& .MuiTextField-root': {m: 1, width: '25ch'},
                    }}
                    noValidate
                    autoComplete="off"
                >
                    <InputLabel shrink id="demo-simple-select-placeholder-label-label">
                        Frequency Range
                    </InputLabel>
                    <Slider style={{marginTop: "24px"}}
                            value={value}
                            onChange={handleChange}
                            valueLabelDisplay="auto"
                            aria-labelledby="range-slider"
                            marks={marks}
                    />
                </Box>
            </FormControl>
        );
    } else {
        return <></>
    }
};


export default SoundRange;