import React from 'react';
import InputLabel from '@mui/material/InputLabel';
import FormControl from '@mui/material/FormControl';
import Slider from '@mui/material/Slider';
import Box from "@mui/material/Box";


const Height = (classes) => {


    const handleHeight = (value) => {
        if (localStorage.getItem('height') != value) {
            localStorage.setItem('height', value);
        }
    }

    const marks = [
        {
            value: 1,
            label: '1',
        },
        {
            value: 100,
            label: '100',
        },
    ];

    return (
        <FormControl className={"all-form-inputs " + classes.formControl}>
            <Box
                component="form"
                sx={{
                    '& .MuiTextField-root': {m: 1, width: '25ch'},
                }}
                noValidate
                autoComplete="off"
            >
                <InputLabel shrink id="demo-simple-select-placeholder-label-label">
                    Height
                </InputLabel>
                <Slider style={{marginTop: "24px"}}
                        getAriaValueText={handleHeight}
                        aria-labelledby="discrete-slider"
                        valueLabelDisplay="auto"
                        step={1}
                        marks={marks}
                        min={0}
                        max={100}
                        defaultValue={localStorage.getItem('height') === null ? 50 : localStorage.getItem('height')}
                />
            </Box>
        </FormControl>
    );
};


export default Height;